<template>
    <div class="order" :class="{showSearch:showSearch}">
        <div class="search-box">
            <el-input :placeholder="$L('输入订单编号')" size="small" v-model="inputValue" class="input-with-select"
                      clearable
                      @keyup.enter.native="$event.target.blur"
                      @change="searchFunc">
                <el-button slot="append" icon="el-icon-search"></el-button>
            </el-input>
            <div class="search-close" @click="doSearch(false)"><i class="el-icon-close"></i></div>
        </div>
        <vue-scroll>
            <div class="main-table" v-if="tableData && tableData.hasOwnProperty('list')" v-loading="loading">
                <div class="table-box" v-for="(item,index) in tableData.list" :key="index">
                    <div class="box">
                        <div class="top">
                            <span>{{$L('订单编号')}}:{{item.orderSn}}</span><span style="float: right;" :class="statusFunc(item.pendingStatus).color">{{ statusFunc(item.pendingStatus).text }}</span>
                        </div>
                        <div class="item" v-for="(i,k) in item.productList">
                            <div class="l-box">
                                <img class="img" :src="i.img">
                                <div class="msg">
                                    <p class="tit mainFontColor">{{ i.prodName }}</p>
                                </div>
                            </div>
                            <div class="r-box">
                                <p>{{i.goodsPrice}}</p>
                                <p>x{{i.goodsNum}}</p>
                            </div>
                        </div>
                        <div class="total">
                            <span>{{ $L('共')}}{{item.countNum}}{{ $L('件商品')}} {{ $L('合计')}}:{{item.orderAmount}}</span>
                        </div>
                    </div>
                    <div class="bottom">
                        <span class="relation">{{ $L('关系')}}：{{ item.relation }}</span>
                        <span>{{ $L('下单用户')}}：{{item.username}}</span><br>
                        <span class="commission">{{ $L('所得佣金')}} <b class="font-red">{{item.commissionPrice}}</b></span>
                    </div>
                </div>

                <!-- 分页 -->
                <div class="list-pages" v-if="tableData.allNum > 0">
                    <el-pagination
                            background
                            small
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-sizes="[5,10,20,30]"
                            :page-size="pageSize"
                            layout="prev, pager, next"
                            :total="tableData.allNum">
                    </el-pagination>
                </div>
                <div class="no-data" v-else>{{$L('暂无数据')}}</div>
            </div>
        </vue-scroll>
    </div>
</template>

<script>
    export default {
        name: "order",
        components : {

        },
        props : {
            routerData : {
                type : Object,
                default:()=>{
                    return {};
                }
            }
        },
        data () {
            return {
                loading:true,
                showSearch: false,
                theadName:{
                    type:'all',
                    list:[
                        {
                            text: '全部订单',
                            type:'all',
                            func: () => {
                                if(this.$route.params.hasOwnProperty('id')){
                                    this.$router.push({params:{type:'all',id:this.$route.params.id}});
                                }else{
                                    this.$router.push({params:{type:'all'}});
                                }
                            }
                        },
                        {
                            text:this.$L('已结算'),
                            type:'settlement',
                            func: () => {
                                if(this.$route.params.hasOwnProperty('id')){
                                    this.$router.push({params:{type:'settlement',id:this.$route.params.id}});
                                }else{
                                    this.$router.push({params:{type:'settlement'}});
                                }
                            }
                        },
                        {
                            text:this.$L('未结算'),
                            type:'unSettlement',
                            func: () => {
                                if(this.$route.params.hasOwnProperty('id')){
                                    this.$router.push({params:{type:'unSettlement',id:this.$route.params.id}});
                                }else{
                                    this.$router.push({params:{type:'unSettlement'}});
                                }
                            }
                        }
                    ],
                },
                tableData: {},
                pageSize:10,
                currentPage:1,
                userId:'',
                inputValue:'',
            }
        },
        computed:{
            mobileTopBtn: {
                get() {
                    return this.$store.state.mobileTopBtn;
                },
                set(val) {
                    this.$store.commit('setMobileTopBtn', val);
                }
            }
        },
        created() {
            this.makeTop();
            this.getData();
        },
        beforeRouteUpdate(to, from, next) {
            // 在当前路由改变，但是该组件被复用时调用
            // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
            // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
            // 可以访问组件实例 `this`
            this.getData(to);
            this.currentPage = 1;
            this.makeTop();
            next();
        },
        beforeDestroy() {
            //销毁当前页面时，必须清空顶部按钮，不然会影响其他页面
            this.mobileTopBtn = null
        },
        created (){
            this.makeTop();
            this.getData();
        },
        methods : {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData(router) {
                const _self = this;
                _self.loading = true;
                if(!router){
                    router = _self.$route;
                };
                _self.theadName.type = router.params.hasOwnProperty('type') ? router.params.type : 'all';
                _self.userId = router.params.hasOwnProperty('id') ? router.params.id : '';
                let arr = {
                    p : _self.currentPage,
                    num : _self.pageSize,
                    settlementStatus : _self.theadName.type,
                    keyword : _self.inputValue,
                    userId : _self.userId ,
                };
                _self.$api('distributionOrder', arr , function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$router.push({name: 'distributorMain'});
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'distributorMain'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'distributorMain'});
                    });
                });
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.getData();
            },
            handleCurrentChange(val){
                this.currentPage = val;
                this.getData();
            },
            searchFunc(val){
                this.inputValue = val;
                this.getData();
            },
            statusFunc(value){
                let x;
                switch(value){
                    case 0:
                        x = {text:this.$L('未结算'),color:'font-red'};
                        break;
                    default:
                        x = {text:this.$L('已结算'),color:'font-green'};
                        break;
                }
                return x;
            },
            makeTop(){
                this.mobileTopBtn = [
                    {
                        icon: 'bbx-icon-sousuo',
                        func: () => {
                            this.doSearch(true);
                        }
                    },
                ];
            },
            doSearch(type){
                if(type){
                    this.showSearch = true;
                }else{
                    this.searchType = "all";
                    this.showSearch = false;
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/lib.scss";
    .order{
        position: relative;
        background-color: #f2f2f2;
        &.showSearch{
            .search-box{
                top: 0;
            }
            .main-table{
                margin-top: 45px;
            }
        }
        .search-box{
            position: absolute;
            width: 100%;
            height: 45px;
            line-height: 45px;
            background-color: #fff;
            top: -45px;
            left: 0;
            z-index: 100;
            border-bottom: 1px solid #E4E7ED;
            @include diy-animation-show(top);
            .input-with-select{
                width: 260px;
                margin-left: 10px;
            }
            .search-close{
                width: 45px;
                height: 45px;
                text-align: center;
                line-height: 45px;
                position: absolute;
                top: 0;
                right: 0;
                z-index: 100;
                font-size: 18px;
                color: #909399;
                &:active{
                    color: #606266;
                }
            }
        }
        .main-table{
            position: relative;
            z-index: 0;
            padding-top: 10px;
            @include diy-animation-show(margin);
            .table-box{
                background-color: #fff;
                margin-bottom: 10px;
                width: 100%;
                .box{
                    padding: 10px 10px 0;
                    .top{
                        height: 30px;
                        line-height: 30px;
                        margin-bottom: 10px;
                    }
                    .item{
                        overflow: hidden;
                        .l-box{
                            float: left;
                            width: 80%;
                            .img{
                                width: 60px;
                                height: 60px;
                            }
                            .msg{
                                width: 70%;
                                display: inline-block;
                                vertical-align: top;
                                padding-left: 10px;
                                .tit{
                                    text-overflow: ellipsis;
                                    display: -webkit-box;
                                    -webkit-line-clamp: 2;
                                    -webkit-box-orient: vertical;
                                    overflow: hidden;
                                }
                            }
                        }
                        .r-box{
                            float: right;
                            text-align: right;
                        }
                    }
                    .total{
                        text-align: right;
                        line-height: 40px;
                    }
                }
                .bottom{
                    padding: 10px;
                    text-align: right;
                    border-top: 1px solid #E1E1E1;
                    span{
                        display: inline-block;
                        line-height: 25px;
                    }
                    .commission{
                        padding-left: 10px;
                    }
                    .relation{
                        padding-right: 30px;
                    }
                }
                .font-red {
                    color: #ff0000;
                }
                .font-green {
                    color: #00bf00;
                }
            }
            .no-data{
                position: relative;
                width: 100%;
                height: 170px;
                line-height: 170px;
                text-align: center;
                background-color: #ffffff;
            }
            .list-pages{
                text-align: center;
                padding: 10px 0;
            }

        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>